<script>
import md5 from "js-md5";
export default {
  data() {
    return {
      show: false,
      code: "",
      CodeImage: require("../assets/code.png"),
    };
  },
  methods: {
    open() {
      console.log("open");
      this.show = true;
    },
    close() {
      this.show = false;
    },
    checkCode() {
      splitbee.track("Enter code");
      const _this = this;
      if (!this.code)
        return this.$message({
          message: "请输入正确的访问口令",
          center: true,
          type: "error",
        });
      if (md5(this.code) === this.$token || md5(this.code) === this.$admin) {
        this.$message({ message: "访问成功", center: true, type: "success" });
        localStorage.setItem("token", md5(this.code));
        setTimeout(function () {
          _this.close();
          _this.successGo();
        }, 2000);
      } else {
        return this.$message({
          message: "口令错误或口令已过期",
          center: true,
          type: "error",
        });
      }
    },
  },
};
</script>
