<template>
  <div class="container dialog-container" v-show="show">
    <div class="mask"></div>
    <div class="dialog-content code-content">
      <span class="btn-close" @click="close"
        ><i class="el-icon-close"></i
      ></span>
      <h2>𝕎𝕚𝕕𝕧𝕖𝕣𝕤𝕖 · 会员绑定</h2>
      <p class="msg">
        由于近期模板二次分享和盗售的情况频发，所以需要绑定<strong
          >您的会员订单信息～</strong
        >
      </p>
      <el-form ref="form" :model="form" label-width="100px">
        <el-form-item label="订单号">
          <el-input v-model="form.code"></el-input>
        </el-form-item>
        <el-form-item label="订单手机号">
          <el-input v-model="form.phone"></el-input>
        </el-form-item>
      </el-form>
      <el-button
        type="primary"
        class="bind-btn"
        :loading="isLoading"
        @click="handleBind"
        >{{ isLoading ? "绑定中..." : "提交绑定" }}</el-button
      >
      <br />
      <p class="msg">
        小红书：<a
          target="blank"
          href="https://www.xiaohongshu.com/user/profile/5c049fc9000000000800af53?xhsshare=WeixinSession&appuid=5c049fc9000000000800af53&apptime=1664074455"
          >@肥汪卷</a
        >
        ｜ B站：<a
          href="https://space.bilibili.com/19225439?spm_id_from=333.1007.0.0"
          target="blank"
          >@阿汪同学</a
        >
        | 微信公众号：<a href="#">汪汪汪世界</a>
      </p>
    </div>
  </div>
</template>
<script>
import md5 from "js-md5";
import checkVipUser from "../constant/vips";
export default {
  data() {
    return {
      show: false,
      isLoading: false,
      form: {
        code: "",
        phone: "",
      },
    };
  },
  methods: {
    open() {
      console.log("open");
      this.show = true;
    },
    close() {
      this.show = false;
    },
    handleBind() {
      const { code, phone } = this.form,
        _this = this;
      if (!code || !phone)
        return this.$message.error("请输入正确的订单号和手机号");

      splitbee.track("Bind vip", {
        code,
        phone,
      });
      this.isLoading = true;
      console.log(code, phone);
      setTimeout(() => {
        const res = checkVipUser(code, phone);
        _this.isLoading = false;
        if (res) {
          return _this.$message[res.type](res.msg);
        }
      }, 1000);
    },
    checkCode() {
      splitbee.track("Enter code");
      const _this = this;
      if (!this.code)
        return this.$message({
          message: "请输入正确的访问口令",
          center: true,
          type: "error",
        });
      if (md5(this.code) === this.$token || md5(this.code) === this.$admin) {
        this.$message({ message: "访问成功", center: true, type: "success" });
        localStorage.setItem("token", md5(this.code));
        setTimeout(function () {
          _this.close();
          _this.successGo();
        }, 2000);
      } else {
        return this.$message({
          message: "口令错误或口令已过期",
          center: true,
          type: "error",
        });
      }
    },
  },
};
</script>
